  constructor(
    private authService:AuthService,
    private router:Router,
    private flashMessage:FlashMessagesService,
  ) { }

  ngOnInit() {
    this.authService.getProfile().subscribe(profile => {
      this.user = profile.user;
      let tasks = this.user.tasks;
      this.survey_completed = tasks[tasks.map(e => e.task_tag).indexOf('survey1')].completed ? true : false;

      let register_date = moment(this.user.register_date)

      // if (!moment(this.current_date).isSame(register_date.add({weeks:this.user.role == 'regulator' ? 2 : 0}), 'day')) this.router.navigate(['/dashboard']);
    })
  }
  nextTab() { this.selected_tab += 1; }

  tabClicked(val:number) { this.selected_tab=val; }

  validateChoices(callback) {
    this.choices.forEach((choice, index) => {
      this.choices_check[index] = choice == null ? 'Please answer this question' : true;
    });
    this.choices_check[0] = this.choices[0] == null ? 'Please answer this question' : parseInt(this.choices[0]) && parseInt(this.choices[0]) > 1900 && parseInt(this.choices[0]) < 2010 ? true : 'Please input valid year';
    this.choices_check[3] = this.choices[3] == null ? 'Please answer this question' : parseInt(this.choices[3]) && parseInt(this.choices[3]) > 0 && parseInt(this.choices[3]) < 200 ? true : 'Please input valid interest rate';
    this.choices_check[6] = this.choices[6] == null ? 'Please answer this question' : parseInt(this.choices[6]) && parseInt(this.choices[6]) > 20 && parseInt(this.choices[6]) < 400 ? true : 'Please input valid height';
    this.choices_check[7] = this.choices[7] == null ? 'Please answer this question' : parseInt(this.choices[7]) && parseInt(this.choices[7]) > 20 && parseInt(this.choices[7]) < 400 ? true : 'Please input valid weight';
    this.choices_check[7] = this.choices[7] == null ? 'Please answer this question' : parseInt(this.choices[7]) && parseInt(this.choices[7]) > 20 && parseInt(this.choices[7]) < 400 ? true : 'Please input valid weight';
    this.choices_check[10] = this.choices[10] == null ? 'Please answer this question' : parseInt(this.choices[10]) && parseInt(this.choices[10]) >= 0 && parseInt(this.choices[10]) <= 100 ? true : 'Please input a number between 0 and 100';

    let validated = this.choices_check.every(e=>e===true);
    callback(validated);
  }
  setNext() {
    if (this.choices[4] == 'never') {
      this.choices[5] = "neverloaned";
    }
  }

  completeSurvey() {
    this.validateChoices((validated:boolean) => {
      if (validated) {
        let submit_choices = [];
        this.choices.forEach((choice, i) => {
          submit_choices.push({name:this.names[i], answer:choice})
        })
        this.authService.completeSurvey(this.user, '1', submit_choices).subscribe(response => {
          if(response.success) {
            this.flashMessage.show('Survey completed',{ cssClass:'my-flash-message success-flash', timeout:3000});
            this.router.navigate(['/dashboard']);
          }
        })
      } else {
        let problem_index = this.choices_check.findIndex(e => e != null && e != true);
        if (problem_index < 3) {
          this.selected_tab = 0;
        } else if (problem_index < 10) {
          this.selected_tab = 1;
        }
        document.getElementsByClassName('mat-sidenav-content')[0].scrollTo(0,0)
        this.flashMessage.show('Some of your answers are invalid', { cssClass:'my-flash-message alert-flash', timeout:3000});
      }
    })
  }

